import React from "react";
import { useSearch } from "../Context/search";
import Nav from "../components/Navbar/Nav";
import { NavLink } from "react-router-dom";

const Search = () => {
  const [values] = useSearch();
  return (
    <div>
      <Nav />
      <div className="container p-1">
        <div className="text-center">
          <h1 className="text-uppercase  fw-bold">
            search results for{" "}
            <span className="bg-warning rounded-5 py-2 px-3">
              {values.keyword}{" "}
            </span>
          </h1>
          <h6 className="text-uppercase"> hope you enjoy! </h6>
          <h4 className="py-4">
            {values?.result.length < 1
              ? "No products Found! please try another word."
              : `Found ${values?.result.length}`}
          </h4>
          <div className="d-flex flex-wrap ">
            {values?.result?.map((product) => (
              <>
                <div
                  className="card m-3 "
                  style={{ height: "25rem", width: "20rem" }}
                >
                  <img
                    src={`/api/admin/product/product-photo/${product._id}`}
                    className="card-img-top cardimg    rounded-2"
                    alt={product.productName}
                  />
                  <div className="card-body">
                    <h4 className="card-title text-capitalize  text-center fw-bold">
                      {product.productName.substring(0, 20)}
                    </h4>
                    <hr />

                    <p className="card-text h5">
                      {" "}
                      {product.description.substring(0, 30)}
                    </p>

                    <h5 className="card-text fw-bold">
                      RS {product.price}.00/-
                    </h5>

                    <NavLink to="/productdetail">
                      <button className="btn btn-primary ms-2 px-">
                        More Details
                      </button>
                    </NavLink>
                    <button className="btn btn-secondary  ms-3 px-4">
                      Add To Cart
                    </button>
                  </div>
                </div>
              </>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Search;
